.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #c2c2c2;
  font-weight: 700;
  text-transform: uppercase;
  color: $main-text-color__menu;
  overflow: auto;

  
  &__top {
    position: sticky;
    z-index: 1;
    margin-bottom: 24px;

    @include onTablet {
      margin-bottom: 0;
    }
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    font-size: 16px;
    line-height: 27px;
    color: inherit;
    text-decoration: none;
  }

  &__book-a-test-ride {
    display: block;
    width: fit-content;
    padding-bottom: 8px;
    border-bottom: 1px solid $main-text-color__menu;

    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    color: inherit;
    text-decoration: none;
  }
}
